import {
  Component,
  OnInit,
  ViewChild,
  OnDestroy,
} from '@angular/core';
import * as moment from 'moment';
import * as _ from 'lodash';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { RolesService } from 'src/app/core/services/roles-service/roles.service';
import { SessionUserService } from 'src/app/core/services/session-user-service/session-user.service';
import { UserService } from 'src/app/core/services/user-service/user.service';
import { OrganisationService } from 'src/app/core/services/organisation-service/organisation.service';
import { NavbarButtonsService } from 'src/app/core/services/navbar-buttons-service/navbar-buttons.service';
import { OfferService } from 'src/app/core/services/offer-service/offer.service';
import { AppointmentTypes } from 'src/app/shared/enums/static-strings';
import { Appointment } from 'src/app/shared/models/appointment';
import { User } from 'src/app/shared/models/user';
import { Organisation } from 'src/app/shared/models/organisation';
import { Offer } from 'src/app/shared/models/offer';
import { Subscription } from 'rxjs/internal/Subscription';
import { ToastService } from 'ng-uikit-pro-standard';

@Component({selector: 'app-list-details', templateUrl: './list-details.component.html', styleUrls: ['./list-details.component.scss']})
export class ListDetailsComponent implements OnInit, OnDestroy {
  @ViewChild('organisationModal')public organisationModal;
  @ViewChild('shiftModal')public shiftModal;
  @ViewChild('popTemplate') public popTemplate;
  public appTypes: typeof AppointmentTypes = AppointmentTypes;
  public email = '';
  public telephone = 'asdfsdf';
  appointments: Array < Appointment > = [];
  appointment: Appointment = null;
  checkParticipations = User.checkparticipations;
  myShifts: Array < Appointment > = [];
  editButtonChecked = false;
  deleteButtonChecked = false;
  signInButtonChecked = false;
  exchangeButtonChecked = false;
  signOutButtonChecked = false;
  users: Array < User > = [];
  organisations: Array < Organisation > = [];
  userOrgs: Array < any > = [];
  userShifts: Array < any > = [];
  selectedOrg: Organisation = null;
  selectedShift: Appointment = null;
  checkInButtonClicked = false;
  checkOutButtonClicked = false;
  _allAppointmentsSubscribe: Subscription;
  _routeSubscribe: Subscription;

  public updateAppointmentModal;
  public userManagementModal;

  constructor(private toast: ToastService,
    private appointmentService: AppointmentService,
    private rolesService: RolesService,
    private sessionUser: SessionUserService,
    private userService: UserService,
    private organisationService: OrganisationService,
    public navBarButtons: NavbarButtonsService,
    private offerService: OfferService,
    private route: ActivatedRoute,
    private router: Router,
    private _location: Location) {}

  ngOnInit() {
    this.navBarButtons.changeToDetailsView();
        this._allAppointmentsSubscribe = this
          .appointmentService
          ._allAppointments
          .subscribe(appointments => {
            this.appointmentService.getOwnShifts(appointments);
            this.getMyShifts();
            if (appointments.length > 0) {
              this._routeSubscribe = this
      .route
      .params
      .subscribe(params => {
            const id: number = +params['id'];
            console.log(appointments);
            this.appointment = appointments.find((appointment) => appointment.id === id);
            console.log(this.appointment);
            if (!this.navBarButtons.isOfferView && !this.navBarButtons.isListHistoryView) {
              if (this.appointment.type !== 'FreierDienst') {
                this.checkCoreButtons(this.appointment.owner);
              }
              this.checkSignInButton();
              this.checkExchangeButton();
              this.checkSignOutButton();

            }
          });
            }
      });
    this.getUsersForUpdateModal();
    this.getOrganisationsForUpdateModal();
  }
  getMyShifts(): void {
    console.log('getMyShifts');
    this
      .appointmentService
      ._myShifts
      .subscribe(myShifts => {
        console.log(myShifts);
        this.myShifts = [];
        this.myShifts = myShifts;
        this.setShiftsForExchange(myShifts);
      });
  }
  getUsersForUpdateModal(): void {
    this
      .userService
      ._allUsers
      .subscribe(users => {

        this.users = [];
        this.users = users;
      });
  }
  checkWidth(): boolean {
    return window.innerWidth >= 630
      ? true
      : false;
  }
  checkPlacement() {
    console.log(window);
    console.log(this.popTemplate);
    return 'auto';
  }
  getOrganisationsForUpdateModal(): void {
    this
      .organisationService
      ._allOrganisations
      .subscribe(organisations => {

        this.organisations = [];
        this.organisations = organisations;
      });
  }

  checkDate(deadline: Date): boolean {
    if (deadline != null) {
      return deadline instanceof Date && !isNaN(deadline.getTime());
    }
    return false;
  }
  deleteAppointment() {
    if (this.deleteButtonChecked) {
      this
        .appointmentService
        .appointmentToDelete
        .next(this.appointment);
        this._location.back();

    } else {
      this
        .toast
        .error('Benutzen Sie keine DevTools!');
    }
  }
  checkDeadline(deadline: Date) {
    if (deadline == null) {
      return false;
    }
    return deadline.getTime() - moment()
      .toDate()
      .getTime() >= 0
      ? true
      : false;
  }
  remainingDays(deadLine: Date): string {
    const rDays = moment(deadLine).diff(moment(), 'day');
    const rHours = moment(deadLine).diff(moment(), 'hour');
    return rDays + ' Tage und ' + (rHours - rDays * 24) + ' Stunden';
  }
  checkCoreButtons(owner: User): void {
    if (this.rolesService.isUserBwst && !this.rolesService.isAdminBfk && !this.rolesService.isAdminBwst) {
      this.editButtonChecked = false;
      this.deleteButtonChecked = false;
    } else if (this.rolesService.isUserBfk) {

      if (this.sessionUser.getUser().email.includes(this.appointment.owner.email)) {

        this.editButtonChecked = true;
        this.deleteButtonChecked = true;
      } else {
        this.editButtonChecked = false;
        this.deleteButtonChecked = false;
      }

    } else {
      this.editButtonChecked = true;
      this.deleteButtonChecked = true;
    }

  }
  checkSignInButton(): void {

    if (this.appointment.type !== AppointmentTypes[AppointmentTypes.Dienst]
      && this.appointment.type !== AppointmentTypes[AppointmentTypes.MarktplatzDienst]
      && this.appointment.type !== AppointmentTypes[AppointmentTypes.FreierDienst]) {

      if (User.checkparticipations(this.appointment.participations[0])) {
        if (_.find(this.appointment.participations, [
          'email',
          this
            .sessionUser
            .getUser()
            .email
        ])) {
          this.signInButtonChecked = false;
        } else {
          if (this.checkDeadline(this.appointment.deadline)
          && this.appointment.maxParticipants - this.appointment.participations.length > 0) {
            this.signInButtonChecked = true;
          } else {
            this.signInButtonChecked = false;
          }
        }

      } else {

        if (this.appointment.participations.every(val => this.sessionUser.getUser().memberships.includes(val))) {
          this.signInButtonChecked = false;

        } else {


          if (this.checkDeadline(this.appointment.deadline) &&
           this.appointment.maxParticipants - this.appointment.participations.length > 0
           && this.appointment.maxParticipants - this.appointment.participations.length > 0) {
            this.signInButtonChecked = true;
          } else {
            this.signInButtonChecked = false;
          }
        }
      }

    } else {
      if ((this.rolesService.isAdminBfk || this.rolesService.isUserBfk)
      && !(this.rolesService.isAdminBwst || this.rolesService.isUserBwst)) {
        this.signInButtonChecked = false;
      } else if (this.appointment.type === AppointmentTypes[AppointmentTypes.FreierDienst]
        || this.appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]) {
        this.signInButtonChecked = true;
      }
    }

  }
  checkExchangeButton() {

    if (this.myShifts.length <= 0) {
      this.exchangeButtonChecked = false;
    } else {
      if (this.rolesService.isUserBwst || this.rolesService.isAdminBwst) {
        if (this.appointment.type === AppointmentTypes[AppointmentTypes.Dienst]) {
          this.exchangeButtonChecked = true;
          this.signInButtonChecked = false;
        } else if (this.appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]) {
          this.exchangeButtonChecked = true;
          this.signInButtonChecked = true;
        } else {
          this.exchangeButtonChecked = false;
        }
      }

    }
    console.log(this.exchangeButtonChecked);
  }
  checkSignOutButton(): void {
    this.signOutButtonChecked = false;
    if (this.appointment.type === AppointmentTypes[AppointmentTypes.Dienst]
      || this.appointment.type === AppointmentTypes[AppointmentTypes.FreierDienst]
      || this.appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]) {
      this.signOutButtonChecked = false;
    } else {
      if (User.checkparticipations(this.appointment.participations[0])) {
        if (_.find(this.appointment.participations, [
          'email',
          this
            .sessionUser
            .getUser()
            .email
        ])) {
          this.signOutButtonChecked = true;
        } else {
          this.signOutButtonChecked = false;
        }
      } else {


        console.log(this.appointment.participations.filter(value => {

          return this.checkForParticipation(value);
        }));
        if (this.appointment.participations.filter(value => {

          return this.checkForParticipation(value);
        }).length > 0) {

          this.signOutButtonChecked = true;
        } else {
          this.signOutButtonChecked = false;
        }
      }
    }

  }
  checkInAppointment() {
    if (this.signInButtonChecked === false) {
      this
        .toast
        .error('Benutzen Sie keine DevTools!');
    } else {
      if (this.appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]) {
        this
          .appointment
          .participations
          .push(this.sessionUser.getUser());
        this.appointment.owner = this
          .sessionUser
          .getUser();
        this
          .appointmentService
          .appointmentToUpdate
          .next(this.appointment);
      } else if (this.appointment.type === AppointmentTypes[AppointmentTypes.FreierDienst]) {
        this
          .appointmentService
          ._freeShiftToTake
          .next(this.appointment.id);
      } else {
        if (!!this.appointment.participations[0].email) {
          this.signIntoAppointment(this.sessionUser.getUser(), this.appointment.id);
        } else {
          this.checkOutButtonClicked = false;
          this.checkInButtonClicked = true;
          this.chooseOrg();
        }
      }

    }
  }
  checkForParticipation(participation: any) {
    for (const membership of this.sessionUser.getUser().memberships) {

      if (_.isEqual(membership, participation)) {
        return true;
      }
    }
    return false;

  }
  signIntoAppointment(participant, appointmentId) {
    this
      .appointmentService
      .participateInAppointment(appointmentId, participant.id)
      .subscribe(data => {
        if (data) {
          this
            .appointment
            .participations
            .push(participant);
        } else {
          this
            .toast
            .error('Etwas ist falsch gelaufen bitte Seite neu laden');
        }
      });
  }
  trackByFn(index, user) {
    return user.id; // or item.id
  }
  signOutOfAppointment(participant, appointmentId) {
    this
      .appointmentService
      .unparticipateInAppointment(appointmentId, participant.id)
      .subscribe(data => {

        if (data) {
          _.remove(this.appointment.participations, (val) => _.isEqual(val, participant));
        } else {
          this
            .toast
            .error('Etwas ist falsch gelaufen bitte Seite neu laden');
        }
      });
  }
  checkOutAppointment() {
    if (this.signOutButtonChecked === false) {
      this
        .toast
        .error('Benutzen Sie keine DevTools!');
    } else {
      if (!!this.appointment.participations[0].email) {
        this.signOutOfAppointment(this.sessionUser.getUser(), this.appointment.id);
      } else {
        this.checkInButtonClicked = false;
        this.checkOutButtonClicked = true;
        this.chooseOrg();
      }
    }
  }
  setShiftsForExchange(myShifts: Appointment[]) {
    this.userShifts = [];

    myShifts.forEach(myShift => {
      this
        .userShifts
        .push({
          value: myShift.id,
          label: myShift.title + ' ' + moment(myShift.from).format('DD.MM.YYYY')
        });
    });

  }
  switchAppointment() {
    if (this.appointment.owner.email === this.sessionUser.getUser().email) {
      this
        .appointmentService
        ._shiftToMarket
        .next({appointmentId: this.appointment.id});
    } else {
      this
        .shiftModal
        .show();
    }
  }
  chooseOrg() {
    this.setOrgsForChooseOrgsModal();
    this
      .organisationModal
      .show();
  }
  organisationSelected(e) {
    this.selectedOrg = _.find(this.sessionUser.getUser().memberships, ['id', e.value]);
  }
  shiftSelected(e) {
    this.selectedShift = _.find(this.myShifts, ['id', e.value]);
  }
  pushOrgToAppointment() {


    if (this.checkInButtonClicked) {
      this.signIntoAppointment(this.selectedOrg, this.appointment.id);
    } else if (this.checkOutButtonClicked) {
      this.signOutOfAppointment(this.selectedOrg, this.appointment.id);
    }
    this
      .organisationModal
      .hide();
  }
  createOffer() {
    if (this.selectedShift == null) {
      this
        .toast
        .error('Bitte eine Schicht auswhlen');
    } else {
      const offerToAdd = new Offer(9999,
        'Pending',
        this.selectedShift,
        this.appointment,
        this.sessionUser.getUser(),
        this.appointment.owner);
      this
        .offerService
        ._offerToAdd
        .next(offerToAdd);
      this
        .shiftModal
        .hide();
    }
  }
  setOrgsForChooseOrgsModal() {
    this.userOrgs = [];
    this
      .sessionUser
      .getUser()
      .memberships
      .forEach(org => {
        if (this.checkOutButtonClicked) {
          if (_.find(this.appointment.participations, ['id', org.id])) {
            this
              .userOrgs
              .push({value: org.id, label: org.name});
          }
        } else if (this.checkInButtonClicked) {
          if (!_.find(this.appointment.participations, ['id', org.id])) {
            this
              .userOrgs
              .push({value: org.id, label: org.name});
          }
        }
      });
  }
  onOrgModalHide(e) {
    if (this.checkOutButtonClicked) {
      this.checkOutButtonClicked = false;
    }
    if (this.checkInButtonClicked) {
      this.checkOutButtonClicked = false;
    }
  }
  getUpdateAppointmentModal(e) {
    this.updateAppointmentModal = e;
  }
  showUpdate() {
    if (this.editButtonChecked) {
      this
        .router
        .navigate(['home/appointment/update'], {
          queryParams: {
            AppointmentToUpdate: this.appointment.id
          }
        });
    } else {
      this
        .toast
        .error('Benutzen Sie keine DevTools!');
    }
  }
  getUserMngmtModalId(e) {
    this.userManagementModal = e;
  }
  ngOnDestroy(): void {
    this._allAppointmentsSubscribe.unsubscribe();
    this._routeSubscribe.unsubscribe();
  }

}
