import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  AfterViewInit,
  ChangeDetectorRef
} from '@angular/core';
import * as _ from 'lodash';
import { ActivatedRoute, Router } from '@angular/router';
import { Appointment } from 'src/app/shared/models/appointment';
import { NavbarButtonsService } from 'src/app/core/services/navbar-buttons-service/navbar-buttons.service';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { SessionUserService } from 'src/app/core/services/session-user-service/session-user.service';
import { Subscription } from 'rxjs/internal/Subscription';
import { TabsetComponent } from 'ng-uikit-pro-standard';

@Component({selector: 'app-list-wrapper', templateUrl: './list-wrapper.component.html', styleUrls: ['./list-wrapper.component.scss']})
export class ListWrapperComponent implements OnInit,
OnDestroy, AfterViewInit {
  isShiftView = false;
  isAppointmentView = true;
  appointments: Array < Appointment >;
  filteredAppointments: Array < Appointment >;
  partedAppointments: Array < Appointment[] >;
  myFilteredShifts = new Array < Appointment > ();
  appointmentId: number;
  _routeSubscribe: Subscription;
  _allAppointmentSubscribe: Subscription;
  tabIndex = 1;
  filterReturnObject: {partedAppointments: Array<Appointment[]>, filteredAppointments: Array<Appointment>};
  @ViewChild('AppointmentTabs')public appointmentTabs: TabsetComponent;

  constructor(public navBarButtons: NavbarButtonsService,
    private appointmentService: AppointmentService,
    private sessionUser: SessionUserService,
    private route: ActivatedRoute,
    private router: Router,
    private changeDetector: ChangeDetectorRef) {
      this.navBarButtons.changeToListView();
  }

  ngOnInit() {
    this._allAppointmentSubscribe = this
      .appointmentService
      ._allAppointments
      .subscribe(appointments => {
        this.appointments = [];
        this.appointments = appointments;
        this.fetchAppointments();
      });
    this.navBarButtons.isListHistoryView = false;

  }
  ngAfterViewInit(): void {
    if (this.isShiftView) {
      this.tabIndex = 2;
      this.fetchAppointments();
      this.appointmentTabs.setActiveTab(2);
    }
  }
  tabActivated(e) {
    console.log(e);
    if (e != null) {
      this.isShiftView = false;
    if (e.activeTabIndex === 2) {
      this.navBarButtons.isListHistoryView = true;
      this.isShiftView = false;
      this.isAppointmentView = false;
      this.tabIndex = 3;
    } else if (e.activeTabIndex === 0) {
      this.tabIndex = 1;
      this.navBarButtons.isListHistoryView = false;
      this.isShiftView = false;
      this.isAppointmentView = true;
    } else {
      this.tabIndex = 2;
      this.isShiftView = true;
      this.isAppointmentView = false;
      this.navBarButtons.isListHistoryView = false;
    }
    this.fetchAppointments();
    }
  }
  changeViewRight() {
    if (this.tabIndex === 1) {
      this.tabIndex = 3;
      this.appointmentTabs.setActiveTab(this.tabIndex);
    } else {
      this.tabIndex--;
      this.appointmentTabs.setActiveTab(this.tabIndex);
    }
  }
  changeViewLeft() {
    if (this.tabIndex === 3) {
      this.tabIndex = 1;
      this.appointmentTabs.setActiveTab(this.tabIndex);
    } else {
      this.tabIndex++;
      this.appointmentTabs.setActiveTab(this.tabIndex);
    }
  }
  fetchAppointments() {
    this.assignAppointments();
      if (this._routeSubscribe == null && this.filteredAppointments.length !== 0) {
        this._routeSubscribe = this.route.fragment.subscribe(fragment => {
          if (!!fragment) {
            const fragCopy: number = +fragment;
            this.appointmentId = fragCopy;
            if (_.find(this.filteredAppointments, ['id', fragCopy]) === undefined) {
              this.isShiftView = true;
              this.isAppointmentView = false;
            }
            if (this.isShiftView && this.appointmentTabs != null) {
              this.isAppointmentView = false;
              this.tabIndex = 2;
              this.changeDetector.detectChanges();
              this.appointmentTabs.setActiveTab(2);
              this.assignAppointments();
            }
          }
        });
      }
  }
  assignAppointments(): void {
    this.filterReturnObject = this.appointmentService.filterAppointments(this.appointments, this.tabIndex);
    this.filteredAppointments = this.filterReturnObject.filteredAppointments;
    this.partedAppointments = this.filterReturnObject.partedAppointments;
  }
  ngOnDestroy() {
    this.navBarButtons.isListHistoryView = false;
    this._routeSubscribe.unsubscribe();
    this._allAppointmentSubscribe.unsubscribe();
  }
  navigateToCalendar() {
    this.isAppointmentView = false;
    this.isShiftView = false;
    this.router.navigate(['/home/appointments/calendar'], {queryParams: {tabIndex: this.tabIndex}});
  }

}
