import {Component, OnInit, OnDestroy} from '@angular/core';
import {CalendarEvent, DAYS_OF_WEEK} from 'angular-calendar';
import * as _ from 'lodash';
import * as moment from 'moment';
import { Router, ActivatedRoute } from '@angular/router';
import { Appointment } from 'src/app/shared/models/appointment';
import { RolesService } from 'src/app/core/services/roles-service/roles.service';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { NavbarButtonsService } from 'src/app/core/services/navbar-buttons-service/navbar-buttons.service';
import { AppointmentTypes } from 'src/app/shared/enums/static-strings';
import { Subscription } from 'rxjs/internal/Subscription';
import { ToastService } from 'ng-uikit-pro-standard';
const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  },
  green: {
    primary: '#4caf50',
    secondary: '#4caf50'
  },
  mdb: {
    primary: '#607d8b',
    secondary: '#607d8b'
  }
};

@Component({selector: 'app-calendar', templateUrl: './calendar.component.html', styleUrls: ['./calendar.component.scss']})
export class CalendarComponent implements OnInit, OnDestroy {
  view = 'month';
  count = 0;
  tabIndex = 1;
  filterReturnObject: {partedAppointments: Array<Appointment[]>, filteredAppointments: Array<Appointment>};
  appointments: Array < Appointment >;
  filteredAppointments: Array < Appointment > = [];
  locale: String = 'de-AT';
  weekStartsOn: number = DAYS_OF_WEEK.MONDAY;
  viewDate: Date = new Date();
  events: Array < CalendarEvent > = [];
  clickedEvent: any;
  _routeSubscribe: Subscription;
  _allAppointmentsSubscription: Subscription;
  // Wird auf null gesetzt um zu verhindern das child component vorzeit zum laden
  // anfngt tslint:disable-next-line:max-line-length
  constructor(private rolesService: RolesService,
    private appointmentService: AppointmentService,
    private navbarButtons: NavbarButtonsService,
    private router: Router,
    private toast: ToastService,
    private route: ActivatedRoute) {
  }

  ngOnInit() {
      this._allAppointmentsSubscription = this
      .appointmentService
      ._allAppointments
      .subscribe(appointments => {
        if (appointments.length !== 0) {
          this._routeSubscribe = this.route.queryParams.subscribe(queryParams => {
            this.tabIndex = +queryParams['tabIndex'];
            this.appointments = [];
            if (this.tabIndex === 3) {
              this.navbarButtons.isListHistoryView = true;
            }
            this.appointments = appointments;
            this.fetchEvents();
          });
        }
    });
    this.navbarButtons.changeToCalendarView();
    if (performance.navigation.type === 1) {
      this
        .rolesService
        .setRoles();
    }
  }
  fetchEvents() {
    console.log(this.appointments);
    this.filterReturnObject = this.appointmentService.filterAppointments(this.appointments, this.tabIndex);
    this.filteredAppointments = this.filterReturnObject.filteredAppointments;
    this
      .filteredAppointments
      .forEach(appointment => {
        if (appointment.type === AppointmentTypes[AppointmentTypes.Dienst]
          || appointment.type === AppointmentTypes[AppointmentTypes.FreierDienst]
          || appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]) {
          this
            .events
            .push({id: appointment.id, start: appointment.from, end: appointment.to, title: appointment.title, color: colors.red});

        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Vollversammlung]) {
          this
            .events
            .push({id: appointment.id, start: appointment.from, end: appointment.to, title: appointment.title, color: colors.yellow});

        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Bewerb]) {
          this
            .events
            .push({id: appointment.id, start: appointment.from, end: appointment.to, title: appointment.title, color: colors.blue});

        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Weiterbildung]) {
          this
            .events
            .push({id: appointment.id, start: appointment.from, end: appointment.to, title: appointment.title, color: colors.mdb});
        } else if (appointment.type === AppointmentTypes[AppointmentTypes.Lehrgang]) {
          this
            .events
            .push({id: appointment.id, start: appointment.from, end: appointment.to, title: appointment.title, color: colors.green});
        }
      });
      this.viewDate = this.filteredAppointments[0].from;
  }
  addAppointment(event) {
    if (event.day.isPast || event.day.isToday) {
      this.toast.error('Keine Termine in der Vergangenheit');
    } else {
      this
        .router
        .navigate(['home/appointment/add'], {
          queryParams: {
            date: moment(event.day.date).format('DD.MM.YYYY').toString()
          }
        });
    }
  }
  eventClicked({event}: {
    event: CalendarEvent
  }): void {
    const appointment = _.find(this.appointments, ['id', event.id]);
    this
      .router
      .navigate(['/home/appointments/details', appointment.id]);

  }
  ngOnDestroy(): void {
    this._allAppointmentsSubscription.unsubscribe();
    this._routeSubscribe.unsubscribe();
  }
}
